<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="pagebg"></div>
      <div class="top animated fadeInDown">
        <img src="@/assets/hua1.png" alt />
      </div>
      <div class="lang animated zoomIn">
        <img src="@/assets/lang.png" alt />
      </div>

      <div class="info">
        <div class="title1 animated zoomIn">诚 邀 您</div>
        <div class="txt animated fadeInDown">
          世界上最美的一刻
          <br />莫过于和我心爱的人牵着彼此的手
          <br />决定编织一段甜蜜的温馨生活
          <br />沉浸在幸福中的我们将于
          <br />2020年10月1日（ 九月十六）
          <br />举行婚礼
        </div>
        <div class="title2 animated zoomIn">新郎：玖玖 新娘：果果</div>
        <div class="txt2 animated fadeInUp">
          席设：安徽省合肥市万达威斯汀酒店
          <br />时间：2020年10月1日 13时14分
          <br />期盼你的到来
          <br />与我们一起见证幸福
        </div>
      </div>
      <div class="bottoms">
        <img class="xing animated zoomIn" src="@/assets/xing3.png" alt />
        <img class="lang1 animated flipInY" src="@/assets/lang2.png" alt />
      </div>
      <div class="b_left animated fadeInLeft">
        <img src="@/assets/hua2.png" alt />
      </div>
    </div>
    <div class="b_right animated fadeInRight">
      <img src="@/assets/hua3.png" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "page2",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
  @include fullsc();
  background: #fff;
  overflow: hidden;
  .page {
    @include wh(100%, auto);
    @include center();
    overflow: hidden;
    //animation: 1s ease 0.2s 1 normal both running zoomIn;
    .pagebg {
      @include fullsc();
      $url: "../../assets/bg1.png";
      @include bis($url);
      animation: 1s ease 0.2s 1 normal both running zoomIn;
    }
    .top {
      @include wh(100%, 20.205vh);
      animation: 1s ease 0.6s 1 normal both running fadeInDown;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .lang {
      position: absolute;
      top: 2vh;
      left: 0;
      right: 0;
      bottom: 0;
      animation: 1s ease 0s 1 normal both running zoomIn;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .info {
      position: relative;
      @include wh(100%, auto);
      .title1 {
        $url: "../../assets/bg2.png";
        @include bis($url);
        display: block;
        margin: 0 auto;
        @include wh(56.405vw, 5.622vh);
        @include font(1.1rem, 5.622vh, "宋体");
        text-align: center;
        color: rgb(104, 60, 55);
        font-weight: bold;
        animation: 1s ease 0.9s 1 normal both running zoomIn;
      }
      .txt {
        text-align: center;
        @include font(0.7rem, 1.3rem, "宋体");
        color: rgb(104, 60, 55);
        margin-top: 3vh;
        animation: 1s ease 1.2s 1 normal both running fadeInDown;
      }
      .title2 {
        text-align: center;
        @include font(0.7rem, 9vh, "宋体");
        color: rgb(104, 60, 55);
        animation: 1s ease 1.4s 1 normal both running zoomIn;
      }
      .txt2 {
        text-align: center;
        @include font(0.7rem, 1.3rem, "宋体");
        color: rgb(104, 60, 55);
        animation: 1s ease 1.8s 1 normal both running fadeInUp;
      }
    }
    .bottoms {
      position: relative;
      @include wh(100%, auto);
      text-align: center;
      .xing {
        @include wh(20.312vw, 4.919vh);
        display: block;
        margin: 2vh auto 0;
        animation: 1s ease 1.2s 1 normal both running zoomIn;
      }
      .lang1 {
        @include wh(53.333vw, 5.622vh);
        margin-top: 1vh;
        animation: 1s ease 1.6s 1 normal both running flipInY;
      }
    }
    .b_left {
      position: absolute;
      bottom: 0;
      left: -5vw;
      @include wh(35.9386vw, 26.6866vh);
      animation: 1s ease 0.6s 1 normal both running fadeInLeft;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .b_right {
    position: absolute;
    right: -4rem;
    bottom: -2rem;
    @include wh(10.4885rem, 10.4885rem);
    animation: 1s ease 0.6s 1 normal both running fadeInRight;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>


