<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="pagebg"></div>
      <div class="top animated fadeInDown">
        <img src="@/assets/hua1.png" alt />
      </div>
      <div class="lang animated zoomIn">
        <img src="@/assets/lang.png" alt />
      </div>
      <div class="info">
        <div class="title animated flipInY">珍藏我们の每一个瞬间</div>
        <div class="photo animated fadeInLeft">
          <img class="photoimg animated fadeInRight" src="@/assets/photo2.jpg" alt />
          <img class="hua animated zoomIn" src="@/assets/hua4.png" alt />
        </div>
        <div class="jie animated fadeInDown">
          <img src="@/assets/jie1.png" alt />
        </div>
        <div class="photowrap">
          <img class="love1 animated slideRight" src="@/assets/love1.png" alt />
          <img class="love2 animated slideDown" src="@/assets/love2.png" alt />
          <div class="photo2 animated fadeInUp">
            <img class="animated fadeInUp" src="@/assets/photo.jpg" alt="">
          </div>
        </div>
      </div>
      <div class="b_left animated fadeInLeft">
        <img src="@/assets/hua2.png" alt />
      </div>
    </div>
    <div class="b_right animated fadeInRight">
      <img src="@/assets/hua3.png" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "page5",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
  @include fullsc();
  background: #fff;
  overflow: hidden;
  .page {
    @include wh(100%, auto);
    @include center();
    overflow: hidden;
    //animation: 1s ease 0.2s 1 normal both running zoomIn;
    .pagebg {
      @include fullsc();
      $url: "../../assets/bg1.png";
      @include bis($url);
      animation: 1s ease 0.2s 1 normal both running zoomIn;
    }
    .top {
      @include wh(100%, 20.205vh);
      animation: 1s ease 0.6s 1 normal both running fadeInDown;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .lang {
      position: absolute;
      top: 2vh;
      left: 0;
      right: 0;
      bottom: 0;
      animation: 1s ease 0s 1 normal both running zoomIn;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .info {
      position: relative;
      @include wh(100%, auto);
      margin-top: -4vh;
      .title {
        display: block;
        $url: "../../assets/kuang3.png";
        @include bis($url);
        @include wh(63.437vw, 7.028vh);
        margin: 0 auto;
        color: rgb(219, 153, 153);
        font-size: 0.72rem;
        line-height: 7.028vh;
        text-align: center;
        animation: 1s ease 0.8s 1 normal both running flipInY;
      }
      .photo {
        display: block;
        position: relative;
        margin: 0 auto;
        $url: "../../assets/kuang4.png";
        @include bis($url);
        @include wh(85vw, 31.712vh);
        @include fj(center);
        animation: 1s ease 1.2s 1 normal both running fadeInLeft;
        align-items: center;
        .photoimg {
          @include wh(75.938vw, 27.581vh);
          animation: 1s ease 1.4s 1 normal both running fadeInRight;
        }
        .hua {
          position: absolute;
          right: -7vw;
          bottom: -6vh;
          transform: rotate(148deg);
          @include wh(25.986vw, 13.715vh);
          animation: 1s ease 1.6s 1 normal both running zoomIn;
        }
      }
      .jie {
        @include wh(100%, auto);
        text-align: center;
        animation: 1s ease 1.8s 1 normal both running fadeInDown;
        img {
          @include wh(24.53vw, 10.541vh);
        }
      }
      .photowrap {
        position: relative;
        @include wh(100%, 28.485vh);
        .love1 {
          position: absolute;
          top: -10vh;
          left: 5vw;
          @include wh(26.405vw, 14.67vh);
          animation: 1s ease 2.4s 1 normal both running slideRight;
        }
        .love2 {
          @include wh(27.813vw, 10.716vh);
           position: absolute;
          top: 3vh;
          left: 6vw;
          animation: 1s ease 2.5s 1 normal both running slideDown;
        }
        .photo2 {
          position: absolute;
          top: -1vh;
          right: 8vw;
          $url: "../../assets/kuang4.png";
          @include bis($url);
          @include wh(56.941vw,21.877vh);
          transform: rotate(175deg);
          @include fj(center);
          align-items: center;
          animation: 1s ease 2s 1 normal both running fadeInUp;
          img{
            @include wh(51.898vw,18.317vh);
            transform: rotate(-181deg);
            animation: 1s ease 2.2s 1 normal both running fadeInUp;
          }
        }
      }
    }

    .b_left {
      position: absolute;
      bottom: 0;
      left: -5vw;
      @include wh(35.9386vw, 26.6866vh);
      animation: 1s ease 0.6s 1 normal both running fadeInLeft;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .b_right {
    position: absolute;
    right: -4rem;
    bottom: -2rem;
    @include wh(10.4885rem, 10.4885rem);
    animation: 1s ease 0.6s 1 normal both running fadeInRight;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>


