<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="pagebg"></div>
      <div class="top animated fadeInDown">
        <img src="@/assets/hua1.png" alt />
      </div>
      <div class="lang animated zoomIn">
        <img src="@/assets/lang.png" alt />
      </div>
      <div class="info">
        <div class="left">
          <div class="leftwrap">
            <div class="photo">
              <img class="bgphoto animated rotateInDownRight" src="@/assets/kuang5.png" alt />
              <div class="imgphotowrap">
                <img class="imgphoto animated rotateInDownLeft" src="@/assets/photo3.jpg" alt />
              </div>
              
            </div>
            <div class="title animated zoomIn">OUR STORY</div>
            <img class="arrow animated zoomIn" src="@/assets/arrow4.png" alt />
            <div class="txt animated fadeInDown">
              Love is not simple
              <br />It's need us
              <br />to run.
            </div>
          </div>
        </div>
        <div class="right">
          <div class="leftwrap">
            <div class="title animated fadeInDown">NICE DAY</div>
            <div class="txt animated zoomIn">
              你来到了我身边<br />并带来了全世界
            </div>
            <img class="arrow animated fadeInRight" src="@/assets/arrow1.png" alt />
            <div class="photo">
              <img class="bgphoto animated rotateInDownLeft" src="@/assets/kuang5.png" alt />
              <div class="imgphotowrap">
                <img class="imgphoto animated rotateInDownLeft" src="@/assets/photo4.jpg" alt />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bottoms">
        <img class="animated fadeInLeft" src="@/assets/che.png" >
      </div>
      <div class="b_left animated fadeInLeft">
        <img src="@/assets/hua2.png" alt />
      </div>
    </div>
    <div class="b_right animated fadeInRight">
      <img src="@/assets/hua3.png" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "page6",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
  @include fullsc();
  background: #fff;
  overflow: hidden;
  .page {
    @include wh(100%, auto);
    @include center();
    overflow: hidden;
    //animation: 1s ease 0.2s 1 normal both running zoomIn;
    .pagebg {
      @include fullsc();
      $url: "../../assets/bg1.png";
      @include bis($url);
      animation: 1s ease 0.2s 1 normal both running zoomIn;
    }
    .top {
      @include wh(100%, 20.205vh);
      animation: 1s ease 0.6s 1 normal both running fadeInDown;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .lang {
      position: absolute;
      top: 2vh;
      left: 0;
      right: 0;
      bottom: 0;
      animation: 1s ease 0s 1 normal both running zoomIn;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .info {
      position: relative;
      @include wh(100%, auto);
      margin-top: -1vh;
      overflow: hidden;
      .left {
        @include wh(50%, auto);
        text-align: right;
        .leftwrap {
          display: inline-block;
          text-align: right;
          .photo {
            display: inline-block;
            position: relative;
            @include wh(42.33vw, 37.77vh);
            .bgphoto {
              @include wh(100%, 100%);
              @include fullsc();
              animation: 1s ease 0.8s 1 normal both running rotateInDownRight;
            }
            .imgphotowrap{
              @include wh(37.968vw, 33.997vh);
              @include center();
              .imgphoto {
                @include wh(37.968vw, 33.997vh);
                object-fit: cover;
                animation: 1s ease 1s 1 normal both running rotateInDownLeft;
              }
            }

          }
          .title {
            text-align: center;
            color: rgb(104, 60, 55);
            font-family: "Ink Free";
            font-weight: bold;
            line-height: 1;
            animation: 1s ease 2.4s 1 normal both running zoomIn;
          }
          .arrow {
            @include wh(40vw, 2.17vh);
            animation: 1s ease 2.6s 1 normal both running zoomIn;
          }
          .txt {
            text-align: left;
            color: rgb(104, 60, 55);
            line-height: 1.5;
            font-size: .75rem;
            animation: 1s ease 2.8s 1 normal both running fadeInDown;
          }
        }
      }
      .right {
        @include wh(50%, auto);
        .leftwrap {
          display: inline-block;
          text-align: left;
          .title {
            text-align: center;
            color: rgb(104, 60, 55);
            font-family: "Ink Free";
            font-weight: bold;
            line-height: 1;
            font-size: 1.2rem;
            animation: 1s ease 1.8s 1 normal both running fadeInDown;
          }
          .txt {
            text-align: center;
            color: rgb(104, 60, 55);
            line-height: 1.5;
            font-size: .8rem;
            margin: 1vh 0;
            animation: 1s ease 2s 1 normal both running zoomIn;
          }
          .arrow {
            @include wh(35.312vw, 3.601vh);
            display: block;
            margin: 0 auto;
            animation: 1s ease 2.2s 1 normal both running fadeInRight;
          }
          .photo {
            position: relative;
            @include wh(42.33vw, 37.77vh);
            // @include fj(center);
            // align-items: center;
            .bgphoto {
              @include wh(100%, 100%);
              @include fullsc();
              animation: 1s ease 1.2s 1 normal both running rotateInDownLeft;
            }
            .imgphotowrap{
              @include wh(37.968vw, 33.997vh);
              @include center();
              .imgphoto {
              @include wh(37.968vw, 33.997vh);
              object-fit: cover;
              animation: 1s ease 1.4s 1 normal both running rotateInDownLeft;
            }
            }
            
          }
        }
      }
    }
    .bottoms{
      position: relative;
      @include wh(100%,19.49vh);
      img{
        position: absolute;
        left: 50%;
        margin-left: -32.812vw;
        top: -4vh;
        @include wh(65.624vw,23.016vh);
        animation: 2s ease 3s 1 normal both running fadeInLeft;
      }
    }

    .b_left {
      position: absolute;
      bottom: 0;
      left: -5vw;
      @include wh(35.9386vw, 26.6866vh);
      animation: 1s ease 0.6s 1 normal both running fadeInLeft;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .b_right {
    position: absolute;
    right: -4rem;
    bottom: -2rem;
    @include wh(10.4885rem, 10.4885rem);
    animation: 1s ease 0.6s 1 normal both running fadeInRight;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>


