<template>
  <div class="swiper-slide">
    <div class="page">
      <div class="pagebg"></div>
      <div class="top animated fadeInDown">
        <img src="@/assets/hua1.png" alt />
      </div>
      <div class="lang animated zoomIn">
        <img src="@/assets/lang.png" alt />
      </div>

      <div class="info">
        <div class="photo animated rotateIn">
          <img src="@/assets/photo2.jpg" alt />
        </div>
        <div class="title1 animated zoomIn">-THE BRIDE-</div>
        <div class="kuang animated zoomIn">
          <span class="span1 animated fadeInRight">新娘</span>
          <span class="span2 animated fadeInLeft">果果</span>
        </div>
        <div class="txt animated fadeInDown">
          最美的风景是有你在的地方
          <br />一起踏上属于我们的前方
        </div>
        <div class="kuang2 animated zoomIn">SAVE THE DATE</div>
      </div>

      <div class="bottoms">
        <div class="arrows">
          <img class="arrows_1 animated fadeInLeft" src="@/assets/arrow1.png" alt />
          <img class="arrows_2 animated fadeInRight" src="@/assets/arrow2.png" alt />
        </div>
      </div>

      <div class="b_left animated fadeInLeft">
        <img src="@/assets/hua2.png" alt />
      </div>
    </div>
    <div class="b_right animated fadeInRight">
      <img src="@/assets/hua3.png" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "page2",
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/style/mixin";
@import "src/style/ani";

.swiper-slide {
  @include fullsc();
  background: #fff;
  overflow: hidden;
  .page {
    @include wh(100%, auto);
    @include center();
    overflow: hidden;
    //animation: 1s ease 0.2s 1 normal both running zoomIn;
    .pagebg {
      @include fullsc();
      $url: "../../assets/bg1.png";
      @include bis($url);
      animation: 1s ease 0.2s 1 normal both running zoomIn;
    }
    .top {
      @include wh(100%, 20.205vh);
      animation: 1s ease 0.6s 1 normal both running fadeInDown;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .lang {
      position: absolute;
      top: 2vh;
      left: 0;
      right: 0;
      bottom: 0;
      animation: 1s ease 0s 1 normal both running zoomIn;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .info {
      position: relative;
      @include wh(100%, auto);
      margin-top: -4vh;
      .photo {
        display: block;
        margin: 0 auto;
        @include wh(50.312vw, 27.76vh);
        animation: 1s ease 0.8s 1 normal both running rotateIn;
        img {
          @include wh(100%, 100%);
          object-fit: cover;
        }
      }
      .title1 {
        $url: "../../assets/bg2.png";
        @include bis($url);
        display: block;
        margin: 0 auto;
        @include wh(56.405vw, 5.622vh);
        @include font(1.1rem, 5.622vh, "宋体");
        text-align: center;
        color: rgb(104, 60, 55);
        font-weight: bold;
        animation: 1s ease 1.2s 1 normal both running zoomIn;
      }
      .kuang {
        display: block;
        margin: 4vh auto;
        @include wh(59.688vw, 4.392vh);
        $url: "../../assets/arrow3.png";
        @include bis($url);
        @include fj(space-around);
        animation: 1s ease 1.4s 1 normal both running zoomIn;
        span {
          margin-top: -1.8vh;
          color: rgb(104, 60, 55);
          @include font(0.9rem, 2, "宋体");
          font-weight: bold;
        }
        .span1{
              animation: 1s ease 1.6s 1 normal both running fadeInRight;
        }
        .span2{
          animation: 1s ease 1.6s 1 normal both running fadeInLeft;
        }
      }
      .txt {
        text-align: center;
        @include font(0.7rem, 2.2, "宋体");
        color: rgb(104, 60, 55);
        margin-top: 3vh;
        animation: 1s ease 1.2s 1 normal both running fadeInDown;
      }
      .kuang2 {
        display: block;
        margin: 2vh auto;
        $url: "../../assets/kuang3.png";
        @include bis($url);
        @include wh(54.376vw, 5.973vh);
        line-height: 5.793vh;
        text-align: center;
        color: rgb(219, 153, 153);
        font-size: 0.75rem;
        animation: 1s ease 2.4s 1 normal both running zoomIn;
      }
    }

    .bottoms {
      text-align: center;
      margin-bottom: 5vh;
      .arrows {
        margin: 1vh 0;
        img {
          @include wh(25.312vw, 2.547vh);
        }
        .arrows_1 {
          animation: 1s ease 2.4s 1 normal both running fadeInLeft;
        }
        .arrows_2 {
          animation: 1s ease 2.4s 1 normal both running fadeInRight;
        }
      }
    }

    .b_left {
      position: absolute;
      bottom: 0;
      left: -5vw;
      @include wh(35.9386vw, 26.6866vh);
      animation: 1s ease 0.6s 1 normal both running fadeInLeft;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .b_right {
    position: absolute;
    right: -4rem;
    bottom: -2rem;
    @include wh(10.4885rem, 10.4885rem);
    animation: 1s ease 0.6s 1 normal both running fadeInRight;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
</style>


